    &__container {
        position: relative;

        /* Screen height fixes due to UI space restrictions */
        @media only screen and (max-height: 520px) {
            transform: scale(0.85);
            transform-origin: bottom;
            padding: 0 0.8rem !important;
        }
        @media only screen and (max-height: 480px) {
            transform: scale(0.75);
        }
    }
    &__tooltip-container {
        position: absolute;
        z-index: 2;
        top: -10px;
        right: 10px;
    }
    &__digit {
        pointer-events: none;
        margin: 0 0.6em;
        text-align: center;
        position: relative;
        transition: transform 0.25s linear;

        &--latest {
            z-index: 1;
            transform: scale(1.2);

            .digits__digit-spot {
                font-size: 0.9em;
            }
        }
        &--win {
            z-index: 1;
            transform: scale(1.25);

            .digits__pie-container:before {
                box-shadow: 0px 1px 18px var(--text-profit-success);
            }
        }
        &--loss {
            z-index: 1;
            transform: scale(1.25);

            .digits__pie-container:before {
                top: -2px;
                left: -2px;
                box-shadow: 0px 1px 18px var(--text-loss-danger);
                border: 3px solid var(--text-loss-danger);

                @include mobile {
                    top: -1px;
                    left: -3px;
                }
            }
        }
        &--is-selected {
            .digits__digit-display {
                &-value,
                &-percentage {
                    color: $color-white;
                }
            }
            .progress__bg {
                fill: $color-blue-2;
            }
        }
        &--is-selectable {
            pointer-events: auto;

            &:focus,
            &:active,
            &:hover {
                .digits__digit-display {
                    &-value,
                    &-percentage {
                        color: $color-white;
                    }
                }
                .progress__bg {
                    fill: $color-blue-2;
                }
            }
        }
        &-display-value {
            transition: transform 0.25s linear;
            position: absolute;
            transform: scale(0.9);
            top: 4px;
            color: var(--text-prominent);

            &--no-stats {
                transform: scale(1) translateY(5px);
            }

            @include mobile {
                top: 10px;
                transform: none;
                font-size: 1.4rem;
                line-height: 1.43;

                &--no-stats {
                    top: 15px;
                }
            }
        }
        &-display-percentage {
            top: 20px;
            position: absolute;
            font-size: 0.65em;
            font-weight: 400;
            color: var(--text-general);

            @include mobile {
                top: 25px;
                transform: none;
                font-size: 1rem;
                line-height: 1.4;
            }
        }
        &-value {
            @include typeface(--paragraph-center-bold-black);
            width: 40px;
            height: 40px;
            background-color: var(--general-main-1);
            color: var(--text-prominent);
            margin-bottom: 0.5em;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: background-color 0.2s ease-out, transform 0.1s ease-out;

            &--selected {
                background-color: var(--general-main-2);
            }
            &--win {
                background-color: var(--text-profit-success);
                color: var(--text-prominent);

                .digits__digit-display-value,
                .digits__digit-display-percentage {
                    color: $color-white;
                }
            }
            &--blink {
                animation-duration: 500ms;
                animation-iteration-count: 4;
                animation-timing-function: step-end;
                animation-name: blinking;
            }
            &--loss {
                border: none !important;
                background-color: var(--text-loss-danger);
                color: var(--text-prominent);

                .digits__digit-display-value,
                .digits__digit-display-percentage {
                    color: $color-white;
                }
            }
            @include mobile {
                height: 48px;
                width: 48px;
                margin: 0;
            }
        }
        &-spot {
            position: absolute;
            top: -25px;
            left: -50%;
            right: -50%;
            width: auto;
            white-space: nowrap;

            &-value {
                transform: scale(0.95);
                display: inline-block;
            }
            &-last {
                @include typeface(--paragraph-center-bold-black);
                padding: 0 4px;
                margin-left: 1px;
                border-radius: 50%;
                border: 1px solid $color-blue;
                color: var(--text-prominent);
                background: var(--general-main-2);

                &--selected-win {
                    color: var(--text-profit-success);
                }
                &--win {
                    color: var(--text-colored-background);
                    border-color: var(--text-profit-success);
                    background: var(--text-profit-success);
                }
                &--loss {
                    color: var(--text-colored-background);
                    border-color: var(--text-loss-danger);
                    background: var(--text-loss-danger);
                }
            }
            @include mobile {
                display: flex;
                justify-content: center;
                top: 1.6rem;
                left: 0;
                right: 0;
                margin-top: 4.8rem;
                pointer-events: none;

                &-value,
                &-last {
                    font-size: 2rem;
                }
                &-last {
                    padding: 0 8px;
                }
                &:not(&--is-trading) {
                    position: relative;
                    top: 0.8rem;
                }
                /* iPhone 8 screen height fixes due to UI space restrictions */
                @media only screen and (max-height: 580px) {
                    &--is-trading {
                        &-value,
                        &-last {
                            font-size: 1.4rem;
                        }
                        &-last {
                            padding: 0 7px;
                        }
                    }
                }
                @media only screen and (max-height: 580px) {
                    position: relative;

                    &:not(&--is-trading) {
                        top: 1.4rem;
                    }
                }
                @media only screen and (max-height: 520px) {
                    position: relative;
                    top: 0;
                    margin-top: 1.6rem;

                    &--is-trading {
                        margin-top: auto;
                    }
                }
            }
        }
    }
    &__pie-container {
        position: absolute;
        top: -1px;
        left: -1px;

        &:before {
            position: absolute;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            top: 1px;
            left: 1px;
            content: '';

            @include mobile {
                top: -4px;
                left: -2px;
                width: 48px;
                height: 48px;
            }
        }
        &--selected:before {
            top: -2px;
            left: -2px;
            width: 42px;
            height: 42px;
            border: 2px solid var(--text-profit-success);

            @include mobile {
                top: 0;
                left: -2px;
                width: 48px;
                height: 48px;
            }
        }
    }
    &__pie-progress {
        transform: rotate(-90deg);
        width: 42px;
        height: 42px;

        /* postcss-bem-linter: ignore */
        .progress__bg {
            stroke: var(--general-disabled);
        }
        /* postcss-bem-linter: ignore */
        .progress__value {
            stroke: var(--text-less-prominent);

            /* postcss-bem-linter: ignore */
            &--is-max {
                stroke: var(--text-profit-success);
            }
            /* postcss-bem-linter: ignore */
            &--is-min {
                stroke: var(--text-loss-danger);
            }
        }
        @include mobile {
            width: 48px;
            height: 48px;
            margin-top: 0.2rem;
        }
    }
    &__pointer {
        position: absolute;
        bottom: -12px;
        padding: 0 12px;
        transition: transform 0.25s ease;

        @include mobile {
            left: -2px;
        }
    }
    &__particles {
        position: absolute;
        padding: 0 20px;
        top: 8px;
        transform: rotate(45deg);
        opacity: 0;

        &-particle {
            background: var(--brand-secondary);
            opacity: 0.7;
            border-radius: 50%;
            display: block;
            width: 5px;
            height: 5px;
            position: absolute;
            transition: transform 0.5s ease, opacity 0.5s linear 0.5s;
        }
        &--explode {
            opacity: 1;

            .digits__particles-particle {
                opacity: 0;

                &:nth-child(1) {
                    transform: translate(45px, 45px);
                }
                &:nth-child(2) {
                    transform: translate(45px, 0px);
                }
                &:nth-child(3) {
                    transform: translate(0px, 45px);
                }
                &:nth-child(4) {
                    transform: translate(-45px, 45px);
                }
                &:nth-child(5) {
                    transform: translate(-45px, -45px);
                }
                &:nth-child(6) {
                    transform: translate(-45px, 0px);
                }
                &:nth-child(7) {
                    transform: translate(0px, -45px);
                }
                &:nth-child(8) {
                    transform: translate(45px, -45px);
                }
            }
        }
    }
    &__icon {
        &-color {
            fill: var(--brand-orange);
        }
        &--win .digits__icon-color {
            fill: var(--text-profit-success);
        }
        &--loss .digits__icon-color {
            fill: var(--text-loss-danger);
        }
    }
    @include mobile {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-gap: 2.4rem 1.6rem;
        align-items: center;
        margin: 1.6rem 0;

        &--trade {
            grid-gap: 2rem 1.6rem;
            margin: auto 0;
            transform: scale(1.1);
            transform-origin: bottom;

            @media only screen and (max-height: 580px) {
                transform: unset;
                transform-origin: unset;
            }
            @media only screen and (max-height: 480px) {
                margin: auto 0 4.8rem;
            }
        }
        &__container {
            width: 100%;
            margin: 0;
            padding: 0.8rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        &__tooltip-text {
            font-size: 1.2rem;
            color: var(--text-general);
            line-height: 18px;
            text-align: center;
        }
        &__digit {
            margin: auto;
        }
    }
    @include mobile {
        @at-root .popup-root #{$self}__toast-info {
            top: 10.5rem;
        }
    }
}

@keyframes blinking {
    50% {
        background-color: var(--general-main-1);
        color: var(--text-general);
    }
}
